import React,{useState,useEffect} from 'react';
import { Link } from 'react-router-dom';
import AddCommentForm from '../components/AddCommentForm';
import ArticlesList from '../components/ArticlesList';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import articleContent from './article-content';
import NotFoundPage from './NotFoundPage';

const ArticlePage= ({match}) => {
    const name =match.params.name;
    const article = articleContent.find(a=>a.name === name);

    const[articleInfo,setArticleInfo] = useState({
        upvotes:0,
        comments:[]
    });

    //called when component is rendered
    useEffect(()=>{
        const fetchData = async()=>{
            const result= await fetch(`/api/articles/${name}`)
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
        //setArticleInfo({upvotes:3})
    },[name])

    if( !article) return (<NotFoundPage/>)

    const otherArticles = articleContent.filter(a=>a.name !== name);  
    

    return(
        <>        
        <h1>{article.title}</h1>
        <UpvotesSection articleName = {name}
                        upvotes={articleInfo.upvotes}
                        setArticleInfo = {setArticleInfo}
        ></UpvotesSection>
        {article.content.map((para,key)=>(
            <p key={key}>{para}</p>
        ))}
        <CommentsList comments={articleInfo.comments}></CommentsList>
        <AddCommentForm articleName ={name} setArticleInfo = {setArticleInfo}></AddCommentForm>
        <h3> Other Articles:</h3>        
        <ArticlesList articles={otherArticles}></ArticlesList>
        </>
    );
}
export default ArticlePage;
